*, :before, :after {
  box-sizing: border-box;
}

body {
  background-color: #f0f8ff;
}

img {
  object-fit: cover;
  border: 1px solid #000;
  border: 1px solid var(--ss-border-color);
  border-radius: var(--ss-border-radius);
  display: block;
}

.breed-select {
